body {
  font-family: "Montserrat";
}
.badger-icon {
  width: 60%;
}
#title {
  background-color: #cbf1f5;
  color: #71c9ce;
}
.navbar {
  padding: 0 0 4.5rem;
}
.navbar-brand {
  vertical-align: bottom;
  font-size: 2.5rem;
}
.nav-item {
  padding: 0 18px;
}
.nav-link {
  font-size: 1.2rem;
  color: #71c9ce;
}
.W-Icon {
  width: 52px;
  height: 72px;
}
.icon-text {
  vertical-align: bottom;
}
.intro-message {
  color: #522546;
  font-family: "Alfa Slab One", cursive;
  font-size: 3rem;
  font-weight: 500;
  line-height: 1.5;
  text-align: center;
}
.intro-page {
  padding: 5%;
  text-align: center;
}

.col-lg-6 {
  text-align: center;
  margin: auto 0 auto 0;
}
.container-fluid {
  padding: 2% 15%;
  text-align: left;
}
.sign-btn {
  margin: 5% 3% 5% 0;
}
footer {
  text-align: center;
  padding-top: 100px;
}
